import React from "react";
import { Modal, CloseButton } from "react-bootstrap";
import { useWeb3React } from "@web3-react/core";
import { BsArrowUpRight } from "react-icons/bs";
import * as WalletService from "../../Service/WalletService";
import "./index.scss";

const AccountDetail = ({ show, onHide }) => {
  const { account, library } = useWeb3React();
  const currentProviderName = WalletService.getCurrentProvider(library);
  const { networkName, networkURL } = WalletService.getNetworkNameAndURL(library);
  const handleDisconnectWallet = () => {

  }
  return (
    <Modal
      show={show}
      onHide={onHide}
      aria-labelledby="contained-modal-title-vcenter"
      className="AccountDetail"
      centered
    >
      <Modal.Header className="border-0">
        <CloseButton variant="white" />
      </Modal.Header>
      <Modal.Body className="mt-1 mb-1 px-4">
        <div>
          <div className="ContentTitle">ACCOUNT ADDRESS</div>
          <div className="d-flex align-items-start">
            <div className="Address" onClick={() => window.open(`${networkURL}${account}`)}>
              {account.substring(0, 6) + '...' + account.substring(account.length - 4, account.length)}
              <BsArrowUpRight />
            </div>
          </div>
        </div>
        <div className="mt-4">
          <div className="ContentTitle">CONNECTED TO</div>
          <div className="d-flex align-items-start justify-content-between">
            <div>
              {currentProviderName} - {networkName}
            </div>
            <div className="DisconnectWallet px-2" onClick={handleDisconnectWallet}>
              Disconnect wallet
            </div>
          </div>
        </div>
      </Modal.Body>
      <Modal.Footer className="Transaction d-flex flex-column border-0 align-items-start">
        <strong className="ContentTitle font-weight-bold">
          RECENT TRANSACTIONS
        </strong>
        <div className="ContentTitle">
          CURRENTLY NO ACTIVE TRANSACTIONS...
        </div>
      </Modal.Footer>
    </Modal>
  );
};
export default AccountDetail;
